import $http from "@/common/api/request.js"
import Lines from "../../components/common/line.vue"
export default {
	data() {
		return {
			clientHeight: 0,
			activeIndex: 0,
			leftData: [],
			rightData:[]
		}
	},
	methods: {
		// 左侧点击事件
		changeLeftTab(index,id) {
			// 点击时请求数据
			this.getData(id);
			this.activeIndex = index;
			
		},
		// 请求数据
		getData(id) {
			// 判断是否是重复点击,避免重复请求
			if(id === this.activeIndex+1){
				return ;
			}
			$http.request({
				url: "/goods/list"
			}).then((res) => {
				let leftData = [];
				let rightData =[];
				res.forEach(item => {
					leftData.push({
						id: item.id,
						name: item.name
					})
					if(item.id === this.activeIndex+1){
						rightData.push(item.data);
					}
				})
				this.leftData = leftData;
				this.rightData = rightData;
			})
		}
	},
	// 获取可视高度
	onReady() {
		uni.getSystemInfo({
			success: (res) => {
				this.clientHeight = res.windowHeight - uni.upx2px(80);
			}
		});
		this.getData();
	},
	components: {
		Lines
	},
	onNavigationBarSearchInputClicked() {
		uni.navigateTo({
			url: '../search/search'
		})
	}
}
